import Endpoint from '@/services/Endpoint'

export default {
  fundsTransferLimits (payload) {
    return Endpoint().get('/s1/limit/limit/fundsTransfer', {
      params: payload
    })
  },
  createFundsTransferLimit (payload) {
    return Endpoint().post('/s1/limit/limit/fundsTransfer', payload)
  },
  modifyFundsTransferLimit (payload) {
    return Endpoint().patch('/s1/limit/limit/fundsTransfer', payload)
  }
}
