import LimitService from '@/services/limit/LimitService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let LimitTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultLimitTableFilter: {
      type: String,
      default: null
    },
    defaultLimitTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      limitTableData: [],
      limitTableFilterTimer: null,
      limitTableFilter: this.defaultLimitTableFilter,
      limitTableOrderBy: this.defaultLimitTableOrderBy,
      limitTableContainer: 'loading-container',
      limitTableBusy: false,
      limitTableTotalRows: 0,
      limitTableCurrentPage: 1,
      limitTablePerPage: 10
    }
  },
  computed: {
    limitTableNoDataText: function () {
      return this.limitTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    limitTableTotalPages: function () {
      return Math.ceil(this.limitTableTotalRows / this.limitTablePerPage)
    },
    limitTablePagingInfo: function () {
      return this.pagingInfo(this.limitTableCurrentPage, this.limitTablePerPage, this.limitTableTotalRows)
    }
  },
  methods: {
    loadLimitTableData (loadingContainerId) {
      let self = this
      this.limitTableBusy = true
      this.limitTableContainer = loadingContainerId || this.limitTableContainer
      this.startLoading(this.limitTableContainer)
      LimitService.fundsTransferLimits({
        pageIndex: this.limitTableCurrentPage - 1,
        pageSize: this.limitTablePerPage,
        filter: this.limitTableFilter,
        orderByField: this.limitTableOrderBy
      }).then(function (response) {
        self.stopLoading(self.limitTableContainer)
        self.limitTableBusy = false
        self.limitTableTotalRows = response.data.totalRows
        self.limitTableData = response.data.fundsTransferLimitList || []
      }).catch(function (error) {
        self.stopLoading(self.limitTableContainer)
        self.limitTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshLimitTable () {
      this.loadLimitTableData()
    },
    filterLimitTable (filter) {
      let self = this
      if (this.limitTableFilter !== filter) {
        clearTimeout(this.limitTableFilterTimer)
        this.limitTableFilterTimer = setTimeout(() => {
          self.limitTableFilter = filter
          self.loadLimitTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.limitTableFilterTimer)
      }
    },
    orderLimitTable (orderBy, active) {
      this.limitTableOrderBy = (active ? '' : '-') + orderBy
      this.loadLimitTableData()
    }
  },
  watch: {
    limitTableCurrentPage: function () {
      this.refreshLimitTable()
    },
    limitTablePerPage: function () {
      this.refreshLimitTable()
    }
  }
}

export default LimitTableMixin
